.dialogs {
    display: grid;
    grid-template-columns: 2fr 10fr;
}

.dialogsItems {
    padding: 10px;
}

.dialogsItems .active {
    color: red;
}

.messages {
    padding: 10px;
}

.messageInput {
    margin-bottom: 10px;
}

.submitButton {
    font-size: 14px;
    height: 32px;
    padding: 4px 15px;
    border-radius: 6px;
    background-color: #4096ff;
    box-shadow: 0 2px 0 rgb(0 0 0 / 2%);
    outline: none;
    border: 1px solid transparent;
    cursor: pointer;
    color: #ffffff;
}